  :root {
    /* width: 100%; */
  }
  .intro h2,
  h3 {
    margin-top: 15%;
  }
  .goToProject {
    margin-top: 84px;
  }
  .nav-bar-cont {
    display: none;
  }
  form {
    width: 100%;
  }
  .form-cont section {
    width: 100%;
  }
